.btn-wrapper {
  position: relative;
}

.search-button {
  cursor: pointer;
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 10px 10px #fafaf7;
}

.search-button:after {
  content: "";
  cursor: pointer;
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("pokeball_PNG9.bef0d239.png");
  background-size: cover;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  transition: opacity .1s;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.search-button:before {
  content: "";
  cursor: pointer;
  width: 30px;
  height: 30px;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("pngwing.com.656ec9aa.png");
  background-size: cover;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  transition: opacity .1s;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.search-button.open:before {
  opacity: 1;
}

.search-button.open:after {
  opacity: 0;
}

.toggle-theme-btn {
  cursor: pointer;
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px 10px #fafaf7;
}

.toggle-theme-btn:before {
  content: "";
  cursor: pointer;
  width: 30px;
  height: 30px;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("white-pokemon.127ccb4b.png");
  background-size: cover;
  justify-content: center;
  align-items: center;
  transition: opacity .1s;
  display: flex;
  position: absolute;
}

.toggle-theme-btn:after {
  content: "";
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("dark-pokemon.19b880e3.png");
  background-size: cover;
  justify-content: center;
  align-items: center;
  transition: opacity .1s;
  display: flex;
  position: absolute;
}

.toggle-theme-btn.show:before {
  opacity: 1;
}

.toggle-theme-btn.show:after {
  opacity: 0;
}

